import React from "react";
import "./Header.css";
import avatar from "../../avatar.png";
import github from "./../../icons/github-icon.svg";
import linkedin from "./../../icons/linkedin-icon.svg";
import { GITHUB_URL, LINKEDIN_URL } from "../../constants";

const AboutMe = () => {
  return (
    <div className="aboutMe">
      <div className="title">
        <img src={avatar} alt="avatar"></img>
        <h1>Pavan Patel</h1>
      </div>
      <div className="Info">
        <p>Hey there! I am a Full-Stack Developer and Software Engineer</p>
        <p>I love to Solve puzzle and I like to design, build, test and deploy exceptional products</p>
      </div>
      <div className="footerUrls">
        <a href={GITHUB_URL}>
          <img src={github} alt="github profile"></img>
        </a>
        <a href={LINKEDIN_URL}>
          <img src={linkedin} alt="linkedin profile"></img>
        </a>
      </div>
    </div>
  );
};

export default AboutMe;
